@charset "UTF-8";
/* For all devices */
.field {
  padding: 0 2px;
}
.field:not(:first-child) {
  margin-top: 20px;
}
.field:not(:last-child) {
  margin-bottom: 20px;
}

.field__label {
  display: inline-block;
  font-weight: 700;
}
.field__label span {
  font-weight: 400;
  color: #7b8676;
}

.field__feedback,
.field__error {
  display: none;
  width: 100%;
  margin: 0;
  font-weight: 700;
}

.field__feedback {
  color: #00a000;
}

.field__error {
  color: #f74440;
}

.input {
  width: 100%;
  margin: 5px auto;
  padding: 6px 12px;
  border: solid #7b8676 1px;
  border-radius: 2px;
  background: #ffffff;
  color: #333333;
  font: inherit;
  outline: none;
  transition: all 0.3s ease-in-out;
}
.input:disabled {
  opacity: 0.65;
}
.field .input {
  margin-bottom: 0;
}

.input::placeholder {
  color: #cdcdcd;
}

.input:focus {
  box-shadow: 0 0 8px #0078c9;
  border-color: #0078c9;
}

.input--invalid {
  border-color: #f74440;
}

.input--invalid:focus {
  border-color: #f74440;
  box-shadow: 0 0 8px rgba(247, 68, 64, 0.6);
}

.input--valid {
  border-color: #00a000;
}

.input--valid:focus {
  border-color: #00a000;
  box-shadow: 0 0 8px rgba(0, 160, 0, 0.6);
}

/* Only for Desktops */
@media only screen and (min-width: 1200px) {
  .input {
    padding: 9px 14px;
  }
}
.slide-show {
  margin: 0 -15px;
}

.slide-show__viewport {
  position: relative;
  overflow: hidden;
  transition: all 1s ease-in-out;
}

.slide-show__viewport:hover {
  cursor: pointer;
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.3);
}

.slide-show__slides {
  display: flex;
  position: relative;
  width: 100%;
  left: 0;
  align-items: center;
  transition: left 2s ease;
  z-index: 1;
}

.slide-show__slide {
  position: relative;
  margin: auto;
}

.slide-show__image {
  max-width: 100%;
  max-height: 350px;
  transition: all 1s ease-in-out;
}

.slide-show__image:hover {
  transform: scale(1.1);
}

.slide-show__nav {
  position: absolute;
  top: calc(50% - 15px);
  padding: 0;
  z-index: 2;
}

.slide-show__nav[data-direction=previous] {
  left: 0;
}

.slide-show__nav[data-direction=next] {
  right: 0;
}

.slide-show__nav svg {
  width: 30px;
  height: 30px;
  opacity: 0.8;
  background: #ffffff;
  box-shadow: 0 0 20px 0 rgba(51, 51, 51, 0.3);
  transition: all 0.3s ease-in-out;
}

.slide-show__nav[data-direction=previous] svg {
  border-radius: 0 50% 50% 0;
}

.slide-show__nav[data-direction=next] svg {
  border-radius: 50% 0 0 50%;
}

.slide-show__nav:hover svg,
.slide-show__nav:focus svg {
  opacity: 1;
  cursor: pointer;
  box-shadow: 0 0 20px 0 #333333;
}

.slide-show__nav svg path {
  fill: #333333;
  transition: all 0.3s ease-in-out;
}

.slide-show__bullets {
  margin: 15px auto;
}

.slide-show__bullet {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: auto 8px;
  border-radius: 50%;
  background-color: #e1e1e1;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.slide-show__bullet--active {
  background-color: #333333;
  transform: scale(1.2);
}

.slide-show__bullet:not(.slide-show__bullet--active):hover,
.slide-show__bullet:not(.slide-show__bullet--active):focus {
  background-color: #c8c8c8;
  transform: scale(1.05);
}

.stats__item {
  width: 100%;
  margin-bottom: 14px;
}

.stats__item:last-child {
  margin-bottom: 0;
}

.stats__heading {
  margin: 0;
}

.stats__text {
  margin: 0;
}

/* For Tablets & Desktops */
@media only screen and (min-width: 768px) {
  .stats {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .stats__item {
    display: inline-block;
    width: calc(50% - 20px);
    margin: 0 10px 20px;
  }
  .stats__item:nth-last-child(2):nth-child(odd) {
    margin-bottom: 0;
  }
}
/* Only for Desktops */
@media only screen and (min-width: 1200px) {
  .stats__item {
    width: calc(25% - 20px);
    margin: 0 10px;
  }
}
.timeline__viewport {
  position: relative;
  overflow: hidden;
  transition: all 1s ease-in-out;
  padding: 0 0 40px;
}

.timeline__items {
  position: relative;
  left: 0;
  transition: left 1.2s ease;
}

.timeline__item {
  position: relative;
  padding: 40px 10px;
  display: inline-flex;
  flex-direction: column;
  flex: 0 0 250px;
}
.timeline__item::before {
  position: absolute;
  content: " ";
  top: 50%;
  left: 0;
  right: 0;
  height: 3px;
  background: #ffeb05;
}
.timeline__item:first-child::before {
  left: 50%;
}
.timeline__item:last-child::before {
  right: 50%;
}
.timeline__item::after {
  content: " ";
  position: absolute;
  top: 50%;
  left: calc(50% - 17px);
  margin-top: -15px;
  border: 2px solid #333333;
  border-radius: 50%;
  background-color: #f5f5f5;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  color: #333333;
  z-index: 1;
  width: 34px;
  height: 34px;
  font-size: 18px;
  line-height: 31px;
  transition: all 0.3s ease-in-out;
}
.timeline__item.timeline__item--active::after, .timeline__item.timeline__item--present::after {
  border-color: #ffeb05;
}
.timeline__item > div {
  height: 100%;
}
.timeline__item:nth-child(even) > div {
  transform: rotate(180deg);
}

.timeline__item:nth-child(odd) .timeline__content::before {
  bottom: -10px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #ffffff;
}

.timeline__item:nth-child(even) .timeline__content::before {
  top: -10px;
  bottom: auto;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #ffffff;
}

.timeline__item--school::after {
  content: "\f19d";
}

.timeline__item--coding::after {
  content: "\f121";
}

.timeline__item--baby::after {
  content: "\f77c";
}

.timeline__item--work::after {
  content: "\f108";
}

.timeline__item--football::after {
  content: "\f1e3";
}

.timeline__item--childhood::after {
  content: "\f1b3";
}

.timeline__item--food::after {
  content: "\f2e7";
}

.timeline__item--film::after {
  content: "\f008";
}

.timeline__item--present::after {
  color: #ffeb05;
  box-shadow: 0 0 10px 0 #ffeb05;
}

.timeline__content {
  position: relative;
  margin-bottom: 15px;
  padding: 8px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 0 0 10px 0 rgba(123, 134, 118, 0.1);
}
.timeline__item:nth-child(even) .timeline__content {
  transform: rotate(180deg);
  margin-top: 15px;
  margin-bottom: 0;
}

.timeline__content::before {
  content: " ";
  position: absolute;
  left: calc(50% - 10px);
  width: 0;
  bottom: -10px;
  z-index: 1;
}

.timeline__content p {
  margin: 0;
}

.timeline__date {
  font-weight: 700;
  font-style: italic;
}
.timeline__item:nth-child(even) .timeline__date {
  transform: rotate(180deg);
}

.timeline__navs {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.timeline__nav {
  padding: 5px;
  transition: all 0.3s ease;
}

.timeline__nav svg {
  width: 30px;
  height: 30px;
  vertical-align: middle;
}

.timeline__nav:not(:disabled):hover,
.timeline__nav:not(:disabled):focus {
  box-shadow: 0 0 10px 0 #333333;
}

.timeline__nav svg path {
  fill: #333333;
}

